<template>
  <div class="hello">
    <el-card
      shadow="never"
      style="margin-bottom: 10px"
      body-style="padding: 10px;"
    >
      <el-form :model="form" inline size="small" label-position="right">
        <el-form-item label="月份" prop="date" class="ml-4 mb-0">
          <el-date-picker
            v-model="form.date"
            type="month"
            format="yyyy-M"
            value-format="yyyy-M"
            style="width: 120px"
            :clearable="false"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="讲员" prop="preachers" class="ml-4 mb-0">
          <el-select v-model="form.preachers" multiple collapse-tags clearable>
            <el-option-group
              v-for="group in preacherList"
              :key="group.label"
              :label="group.label"
            >
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item label="分区数" prop="areaNum" class="ml-4 mb-0">
          <el-input-number
            v-model="form.areaNum"
            :min="1"
            :max="5"
            label="分区数"
            style="width: 130px"
            @change="changePartNum"
          ></el-input-number>
        </el-form-item>
        <el-form-item class="ml-2 mb-0">
          <el-button type="primary" @click="generate(true)">随机排班</el-button>
          <el-button type="default" @click="generate()">默认排班</el-button>
          <el-tooltip placement="top">
            <div slot="content">
              派单规则：<br />1.每个小组每月一次圣餐和总会学习；<br />
              2. 其他分享者随机生成
            </div>
            <i class="el-icon-warning-outline ml-3"></i>
          </el-tooltip>
        </el-form-item>
      </el-form>
    </el-card>
    <el-table
      :data="tableData"
      size="small"
      border
      stripe
      highlight-current-row
    >
      <el-table-column
        v-for="(col, index) in columns"
        :key="index"
        :prop="col.prop"
        :label="col.label"
      >
        <template slot-scope="{ row }">
          <template v-if="col.prop === 'name'">
            {{ row[col.prop] }}
            <el-select
              v-model="row.area"
              size="mini"
              placeholder="请选择"
              style="float: right; width: 100px"
            >
              <el-option
                v-for="item in areas"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </template>
          <template v-else>
            <template v-for="preacher in preacherList">
              <template v-for="(item, index) in preacher.options">
                <el-tag
                  v-if="item.value === row[col.prop]"
                  :type="formatTagType(item.value)"
                  :key="index"
                  size="small"
                >
                  {{ item.name }}</el-tag
                >
              </template>
            </template>
          </template>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
const groupList = [
  { name: "朱梅", area: 1 },
  { name: "大琳", area: 1 },
  { name: "朱亚花", area: 1 },
  { name: "华星花", area: 1 },
  { name: "须建芬", area: 2 },
  { name: "姚花琴", area: 2 },
  { name: "王闵夏", area: 2 },
  { name: "赵小萍", area: 2 },
  { name: "赵萍", area: 3 },
  { name: "鲁玉月", area: 3 },
  { name: "李雪清", area: 3 },
  { name: "袁理珍", area: 3 },
  { name: "赵敬兵", area: 3 },
  { name: "王利民", area: 4 },
  { name: "刘彦馨", area: 4 },
  { name: "刘登江", area: 4 },
  { name: "赵其友", area: 4 },
  { name: "陈克君", area: 4 },
];
const preacherList = [
  {
    label: "大组分享",
    options: [
      { name: "圣餐", value: "ShengCan" },
      { name: "总会", value: "ZongHui" },
    ],
  },
  {
    label: "小组分享",
    options: [
      { name: "朱梅", value: "ZhuMei" },
      // { name: "大琳", value: "DaLin" },
      { name: "爱芳", value: "AiFang" },
      { name: "凌莹", value: "LingYing" },
      { name: "须建芬", value: "XuJianfen" },
      { name: "姚花琴", value: "YaoHuaqin" },
      { name: "王闵夏", value: "WangMinxia" },
      { name: "赵小萍", value: "ZhaoXiaoping" },
      { name: "赵萍", value: "ZhaoPing" },
      // { name: "李雪清", value: "LiXueqing" },
      { name: "袁理珍", value: "YuanLizhen" },
      { name: "赵敬兵", value: "ZhaoJingbing" },
      { name: "朱玲", value: "ZhuLing" },
      { name: "刘彦馨", value: "LiuYanxin" },
      { name: "张晶", value: "ZhangJing" },
      { name: "赵其友", value: "ZhaoQiyou" },
      { name: "方志刚", value: "FangZhigang" },
      { name: "陈克君", value: "ChenKejun" },
      { name: "许玉红", value: "XuYuhong" },
    ],
  },
];
export default {
  name: "SermonCalendar",
  data() {
    return {
      form: {
        date: "2022-7",
        preachers: [],
        areaNum: 4,
      },
      preacherList: [...preacherList],
      areas: [
        { label: "1区", value: 1 },
        { label: "2区", value: 2 },
        { label: "3区", value: 3 },
        { label: "4区", value: 4 },
      ],
      tableData: [],
    };
  },
  computed: {
    columns() {
      const columns = [{ label: "组名", prop: "name" }];
      const dateArr = this.form.date.split("-");
      let sundayList = this.getWeekDay(dateArr[0], dateArr[1], 0);
      sundayList.forEach((item) => {
        const obj = { label: item, prop: item };
        columns.push(obj);
      });
      return columns;
    },
  },
  methods: {
    // 获取一个月有多少天
    getMonthLength(date) {
      let d = new Date(date);
      // 获取月份的下个月
      d.setMonth(d.getMonth() + 1);
      // 设置为下个月第一天
      d.setDate("1");
      // 设置时间为下月的前一天
      d.setDate(d.getDate() - 1);
      // 返回天数
      return d.getDate();
    },
    // 获取本月的周日 type 0-周日
    getWeekDay(year, month, type) {
      year = Number(year);
      var arr = [];
      var days = this.getMonthLength(`${year}-${month}`);
      for (var j = 1; j <= days; j++) {
        if (new Date(`${year}-${month}-${j}`).getDay() === type) {
          arr.push(`${year}-${month}-${j}`);
        }
      }
      return arr;
    },
    changeDate() {
      this.init();
    },
    resetData() {
      this.preacherList.forEach((item) => {
        item.options.forEach((i) => {
          this.form.preachers.push(i.value);
        });
      });
      this.tableData = [];

      groupList.forEach((item) => {
        const obj = {};
        this.columns.forEach((i) => {
          if (i.prop === "name") {
            obj["name"] = item.name;
          } else {
            obj[i.prop] = "";
          }
        });
        obj["area"] = item.area;
        this.tableData.push(obj);
      });
    },
    init() {
      const newDate = new Date();
      const year = newDate.getUTCFullYear();
      const month = newDate.getMonth();
      this.form.date = `${year}-${month + 1}`;
      this.resetData();
      this.generate();
    },
    changePartNum(val) {
      let arr = [];
      if (val <= 1) {
        arr = [{ label: "1区", value: 1 }];
      } else {
        for (let i = 1; i <= val; i++) {
          arr.push({ label: i + "区", value: i });
        }
      }
      this.areas = [...arr];
    },
    formatTagType(value) {
      let text = "info";
      if (value === "ShengCan") {
        text = "danger";
      } else if (value === "ZongHui") {
        text = "success";
      }
      return text;
    },
    generate(random) {
      const dateArr = this.form.date.split("-");
      let sundayList = this.getWeekDay(dateArr[0], dateArr[1], 0);
      this.tableData.forEach((item) => {
        sundayList.forEach((i) => {
          item[i] = "";
        });
      });

      // 安排大组
      let randomParts = this.areas.map((i) => i.value);
      if (random) {
        randomParts.sort(function () {
          return Math.random() - 0.5;
        });
      }
      this.tableData.forEach((item) => {
        const index1 = randomParts[item.area - 1];
        const index2 = randomParts[item.area] || randomParts[0];
        const prop1 = this.columns[index1] && this.columns[index1].prop;
        const prop2 = this.columns[index2] && this.columns[index2].prop;
        item[prop1] = "ShengCan";
        item[prop2] = "ZongHui";
      });

      // 组装小组
      let randomPreachers = this.preacherList[1].options.map((i) => i.value);
      if (random) {
        randomPreachers.sort(function () {
          return Math.random() - 0.5;
        });
      }

      let preachIndex = 0;
      sundayList.forEach((item) => {
        this.tableData.forEach((i) => {
          if (i[item]) {
            return;
          }
          if (preachIndex >= randomPreachers.length) {
            preachIndex = 0;
          }
          i[item] = randomPreachers[preachIndex];
          preachIndex++;
        });
      });
    },
  },
  created() {
    this.init();
  },
};
</script>
<style scoped>
.hello {
  padding: 20px;
}
</style>
